.wrapper {
  display: flex;
  margin-bottom: 1rem;
  padding: 2rem;
  border-radius: 2rem;
  background-color: #ffffff;

  & > .cover {
    & > img {
      border-radius: 1rem;
    }
  }

  & > .details {
    display: flex;
    flex-direction: column;
    padding: 1rem 2rem;

    & > .title {
      font-size: 2rem;
    }

    & > .summary {
      margin: 0.5em 0;
    }
  }
}
